import {Swipe,SwipeItem,Popup} from 'mint-ui'
import {Badge,InputNumber} from 'element-ui'
export default {
    name: "good",
    components:{
        Swipe,
        SwipeItem,
        Popup,
        Badge,
        InputNumber
    },
    data(){
        return{
            name: '我是商品名字',
            introduction: '我是商品内容介绍',
            price: '1399',
            selectNum: 1,
            maxNum: 10,
            popupVisible: false,
            add_num: false,
            list: [],
            itemList: []
        }
    },
    created(){
        let that = this;
        for (let i = 0; i < 20; i++){
            that.list.push('/img/item/item_' + i + '.jpg')
        }
        for (let i = 1; i < 4; i++){
            that.itemList.push('/img/item/item/item_' + i + '.jpg')
        }
    },
    methods:{
        turn_add_num(){
            let that = this;
            that.popupVisible = false;
            that.add_num = !that.add_num;
            setTimeout(()=>{
                that.add_num = !that.add_num;
            },1500)
        },
        toPayPage(){
          //这里不用传选中商品id，直接存储在store中
          this.$router.push({name:'payPage'})
        },
        toIndex(){
          //返回首页
          this.$router.push({name:'index'})
        },
        toCart(){
          //跳转到购物车
          this.$router.push({name:'cart'})
        }

    }
}
